import React, { useState, useEffect } from 'react';
import { Plus, Search } from 'lucide-react';
import ShipmentList from './ShipmentList';
import ShipmentModal from './ShipmentModal';

function ShipmentManagement() {
  const [shipments, setShipments] = useState([]);
  const [customers, setCustomers] = useState([]);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [modalMode, setModalMode] = useState('view');
  const [currentShipment, setCurrentShipment] = useState(null);
  const [searchTerm, setSearchTerm] = useState('');

  useEffect(() => {
    // Fetch shipments from your API
    const fetchShipments = async () => {
      try {
        const token = localStorage.getItem('token');
        const response = await fetch('https://zxfunct.netlify.app/api/shipments/', {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        });
        if (response.ok) {
          const data = await response.json();
          setShipments(data);
        } else {
          console.error('Failed to fetch shipments:', response.statusText);
        }
      } catch (error) {
        console.error('Error fetching shipments:', error);
      }
    };

    const fetchCustomers = async () => {
      try {
        const token = localStorage.getItem('token');
        const response = await fetch('https://zxfunct.netlify.app/api/customers/names/', {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        });
        if (response.ok) {
          const data = await response.json();
          setCustomers(data);
        } else {
          console.error('Failed to fetch customers:', response.statusText);
        }
      } catch (error) {
        console.error('Error fetching customers:', error);
      }
    };

    fetchShipments();
    fetchCustomers();
  }, []);

  const handleAddShipment = () => {
    setCurrentShipment({
      items: [{ name: '', quantity: 0, type: 'Piece', price: 0, total: 0 }],
      discountPercentage: 0,
      serviceFee: 10,
      receiveDate: new Date().toISOString().split('T')[0],
      shipmentDestination: 'Delmas',
      status: 'Pending'
    });
    setModalMode('add');
    setIsModalOpen(true);
  };

  const handleEditShipment = (shipment) => {
    setCurrentShipment(shipment);
    setModalMode('edit');
    setIsModalOpen(true);
  };

  const handleViewShipment = (shipment) => {
    setCurrentShipment(shipment);
    setModalMode('view');
    setIsModalOpen(true);
  };

  const handleCloseModal = () => {
    setIsModalOpen(false);
    setCurrentShipment(null);
  };

  const handleSaveShipment = async (shipment) => {
    const token = localStorage.getItem('token');
    const method = modalMode === 'add' ? 'POST' : 'PUT';
    const url = modalMode === 'add' 
      ? 'https://zxfunct.netlify.app/api/shipments/' 
      : `https://zxfunct.netlify.app/api/shipments/${shipment._id}`;

    try {
      const response = await fetch(url, {
        method,
        headers: {
          'Content-Type': 'application/json',
          Authorization: `Bearer ${token}`,
        },
        body: JSON.stringify(shipment),
      });

      if (response.ok) {
        const data = await response.json();
        if (method === 'POST') {
          setShipments([...shipments, data.shipment]);
        } else {
          setShipments(shipments.map(s => s._id === shipment._id ? shipment : s));
        }
      } else {
        console.error('Failed to save shipment:', response.statusText);
      }
    } catch (error) {
      console.error('Error saving shipment:', error);
    }

    setIsModalOpen(false);
  };

  const handleDeleteShipment = async (id) => {
    const token = localStorage.getItem('token');
    try {
      const response = await fetch(`https://zxfunct.netlify.app/api/shipments/${id}`, {
        method: 'DELETE',
        headers: {
          Authorization: `Bearer ${token}`,
        },
      });

      if (response.ok) {
        setShipments(shipments.filter(s => s._id !== id));
      } else {
        console.error('Failed to delete shipment:', response.statusText);
      }
    } catch (error) {
      console.error('Error deleting shipment:', error);
    }

    setIsModalOpen(false);
  };

  return (
    <div className="container mx-auto px-4 py-8">
      <h1 className="text-3xl font-bold mb-6 text-gray-800 dark:text-white">Shipment Management</h1>
      
      <div className="mb-4 flex justify-between items-center">
        <div className="relative">
          <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400" />
          <input
            type="text"
            placeholder="Search shipments"
            className="pl-10 pr-4 py-2 border rounded-md focus:outline-none focus:ring-1 focus:ring-blue-600 dark:bg-gray-700 dark:border-gray-600 dark:text-white"
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
          />
        </div>
        <button
          onClick={handleAddShipment}
          className="px-4 py-2 bg-blue-600 text-white rounded-md hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-blue-600 focus:ring-opacity-50"
        >
          <Plus size={18} className="inline mr-2" />
          Add Shipment
        </button>
      </div>

      <ShipmentList
        shipments={shipments}
        searchTerm={searchTerm}
        onView={handleViewShipment}
        onEdit={handleEditShipment}
        onDelete={handleDeleteShipment}
      />

      {isModalOpen && (
        <ShipmentModal
          shipment={currentShipment}
          customers={customers}
          mode={modalMode}
          onClose={handleCloseModal}
          onSave={handleSaveShipment}
          onDelete={handleDeleteShipment}
        />
      )}
    </div>
  );
}

export default ShipmentManagement;
