// src/components/Dashboard.js
import "react-datepicker/dist/react-datepicker.css";
import React, { useState, useEffect } from 'react';
import { Bar } from 'react-chartjs-2';
import { Chart as ChartJS, CategoryScale, LinearScale, BarElement, Title, Tooltip, Legend } from 'chart.js';
import { Calendar } from 'lucide-react';
import DatePicker from 'react-datepicker';
import "react-datepicker/dist/react-datepicker.css";

ChartJS.register(CategoryScale, LinearScale, BarElement, Title, Tooltip, Legend);

function Dashboard() {
  const [timePeriod, setTimePeriod] = useState('week');
  const [chartData, setChartData] = useState({ labels: [], datasets: [] });
  const [startDate, setStartDate] = useState(new Date(new Date().setMonth(new Date().getMonth() - 1)));
  const [endDate, setEndDate] = useState(new Date());

  useEffect(() => {
    // In a real application, you would fetch this data from your backend
    const generateData = () => {
      switch (timePeriod) {
        case 'week':
          return {
            labels: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun'],
            data: [12, 19, 3, 5, 2, 3, 7]
          };
        case 'month':
          return {
            labels: [...Array(30).keys()].map(i => i + 1),
            data: [...Array(30)].map(() => Math.floor(Math.random() * 50) + 10)
          };
        case 'sixMonths':
          return {
            labels: ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun'],
            data: [65, 59, 80, 81, 56, 55]
          };
        case 'year':
          return {
            labels: ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'],
            data: [65, 59, 80, 81, 56, 55, 40, 45, 60, 70, 75, 90]
          };
        case 'custom':
          const days = (endDate.getTime() - startDate.getTime()) / (1000 * 3600 * 24);
          return {
            labels: [...Array(Math.ceil(days)).keys()].map(i => {
              const date = new Date(startDate);
              date.setDate(date.getDate() + i);
              return date.toLocaleDateString();
            }),
            data: [...Array(Math.ceil(days))].map(() => Math.floor(Math.random() * 50) + 10)
          };
        default:
          return { labels: [], data: [] };
      }
    };

    const { labels, data } = generateData();
    setChartData({
      labels,
      datasets: [
        {
          label: 'Shipments',
          data,
          backgroundColor: 'rgba(75, 192, 192, 0.6)',
        },
      ],
    });
  }, [timePeriod, startDate, endDate]);

  const chartOptions = {
    responsive: true,
    maintainAspectRatio: false,
    plugins: {
      legend: {
        position: 'top',
      },
      title: {
        display: true,
        text: 'Shipment Quantity',
      },
    },
  };

  return (
    <div className="container mx-auto px-4 py-8">
      <h1 className="text-3xl font-bold mb-6 text-gray-800 dark:text-white">Dashboard</h1>
      <div className="grid grid-cols-1 md:grid-cols-3 gap-6 mb-6">
        <div className="bg-white dark:bg-gray-800 p-6 rounded-lg shadow">
          <h2 className="text-xl font-semibold mb-4 text-gray-800 dark:text-white">Total Shipments</h2>
          <p className="text-4xl font-bold text-blue-600">1,234</p>
        </div>
        <div className="bg-white dark:bg-gray-800 p-6 rounded-lg shadow">
          <h2 className="text-xl font-semibold mb-4 text-gray-800 dark:text-white">Pending Shipments</h2>
          <p className="text-4xl font-bold text-yellow-600">56</p>
        </div>
        <div className="bg-white dark:bg-gray-800 p-6 rounded-lg shadow">
          <h2 className="text-xl font-semibold mb-4 text-gray-800 dark:text-white">Completed Shipments</h2>
          <p className="text-4xl font-bold text-green-600">978</p>
        </div>
      </div>
      <div className="bg-white dark:bg-gray-800 p-6 rounded-lg shadow">
        <div className="flex flex-col md:flex-row justify-between items-center mb-4">
          <h2 className="text-xl font-semibold text-gray-800 dark:text-white flex items-center mb-4 md:mb-0">
            <Calendar size={24} className="mr-2" />
            Shipment Trends
          </h2>
          <div className="flex flex-wrap justify-center md:justify-end space-x-2 space-y-2 md:space-y-0">
            <button
              onClick={() => setTimePeriod('week')}
              className={`px-3 py-1 rounded ${
                timePeriod === 'week' ? 'bg-blue-600 text-white' : 'bg-gray-200 dark:bg-gray-700 text-gray-800 dark:text-white'
              }`}
            >
              Week
            </button>
            <button
              onClick={() => setTimePeriod('month')}
              className={`px-3 py-1 rounded ${
                timePeriod === 'month' ? 'bg-blue-600 text-white' : 'bg-gray-200 dark:bg-gray-700 text-gray-800 dark:text-white'
              }`}
            >
              Month
            </button>
            <button
              onClick={() => setTimePeriod('sixMonths')}
              className={`px-3 py-1 rounded ${
                timePeriod === 'sixMonths' ? 'bg-blue-600 text-white' : 'bg-gray-200 dark:bg-gray-700 text-gray-800 dark:text-white'
              }`}
            >
              6 Months
            </button>
            <button
              onClick={() => setTimePeriod('year')}
              className={`px-3 py-1 rounded ${
                timePeriod === 'year' ? 'bg-blue-600 text-white' : 'bg-gray-200 dark:bg-gray-700 text-gray-800 dark:text-white'
              }`}
            >
              Year
            </button>
            <button
              onClick={() => setTimePeriod('custom')}
              className={`px-3 py-1 rounded ${
                timePeriod === 'custom' ? 'bg-blue-600 text-white' : 'bg-gray-200 dark:bg-gray-700 text-gray-800 dark:text-white'
              }`}
            >
              Custom
            </button>
          </div>
        </div>
        {timePeriod === 'custom' && (
          <div className="flex justify-center md:justify-end space-x-4 mb-4">
            <DatePicker
              selected={startDate}
              onChange={date => setStartDate(date)}
              selectsStart
              startDate={startDate}
              endDate={endDate}
              className="px-2 py-1 border rounded"
            />
            <DatePicker
              selected={endDate}
              onChange={date => setEndDate(date)}
              selectsEnd
              startDate={startDate}
              endDate={endDate}
              minDate={startDate}
              className="px-2 py-1 border rounded"
            />
          </div>
        )}
        <div style={{ height: '450px' }}>
          <Bar data={chartData} options={chartOptions} />
        </div>
      </div>
    </div>
  );
}

export default Dashboard;