import React, { useState, useEffect } from 'react';
import { Plus, Search, X, Edit, Trash2 } from 'lucide-react';

function CustomerManagement() {
  const [customers, setCustomers] = useState([]);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [modalMode, setModalMode] = useState('view');
  const [currentCustomer, setCurrentCustomer] = useState({
    firstName: '', lastName: '', email: '', phone: '',
    street: '', city: '', country: '', zipcode: '',
    category: 'Individual', idType: '', idStateCountry: '', idNumber: ''
  });
  const [searchTerm, setSearchTerm] = useState('');
  const [currentPage, setCurrentPage] = useState(1);
  const [customersPerPage] = useState(10);

  useEffect(() => {
    // Fetch customers from your API
    const fetchCustomers = async () => {
      try {
        const token = localStorage.getItem('token');
        const response = await fetch('https://zxfunct.netlify.app/api/customers/', {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        });
        if (response.ok) {
          const data = await response.json();
          setCustomers(data);
        } else {
          console.error('Failed to fetch customers:', response.statusText);
        }
      } catch (error) {
        console.error('Error fetching customers:', error);
      }
    };

    fetchCustomers();
  }, []);

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setCurrentCustomer({ ...currentCustomer, [name]: value });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    const token = localStorage.getItem('token');
    const method = currentCustomer._id ? 'PUT' : 'POST';
    const url = currentCustomer._id
      ? `https://zxfunct.netlify.app/api/customers/${currentCustomer._id}`
      : `https://zxfunct.netlify.app/api/customers/`;

    try {
      const response = await fetch(url, {
        method,
        headers: {
          'Content-Type': 'application/json',
          Authorization: `Bearer ${token}`,
        },
        body: JSON.stringify(currentCustomer),
      });

      if (response.ok) {
        const data = await response.json();
        if (method === 'POST') {
          setCustomers([...customers, { ...currentCustomer, _id: data.insertedId }]);
        } else {
          setCustomers(customers.map(c => c._id === currentCustomer._id ? currentCustomer : c));
        }
      } else {
        console.error('Failed to save customer:', response.statusText);
      }
    } catch (error) {
      console.error('Error saving customer:', error);
    }

    setIsModalOpen(false);
    resetCurrentCustomer();
  };

  const handleRowClick = (customer) => {
    setCurrentCustomer(customer);
    setModalMode('view');
    setIsModalOpen(true);
  };

  const handleEdit = () => {
    setModalMode('edit');
  };

  const handleDelete = async () => {
    const token = localStorage.getItem('token');
    try {
      const response = await fetch(`https://zxfunct.netlify.app/api/customers/${currentCustomer._id}`, {
        method: 'DELETE',
        headers: {
          Authorization: `Bearer ${token}`,
        },
      });

      if (response.ok) {
        setCustomers(customers.filter(c => c._id !== currentCustomer._id));
      } else {
        console.error('Failed to delete customer:', response.statusText);
      }
    } catch (error) {
      console.error('Error deleting customer:', error);
    }

    setIsModalOpen(false);
    resetCurrentCustomer();
  };

  const resetCurrentCustomer = () => {
    setCurrentCustomer({
      firstName: '', lastName: '', email: '', phone: '',
      street: '', city: '', country: '', zipcode: '',
      category: 'Individual', idType: '', idStateCountry: '', idNumber: ''
    });
  };

  const filteredCustomers = customers.filter(customer =>
    `${customer.firstName} ${customer.lastName}`.toLowerCase().includes(searchTerm.toLowerCase())
  );

  const indexOfLastCustomer = currentPage * customersPerPage;
  const indexOfFirstCustomer = indexOfLastCustomer - customersPerPage;
  const currentCustomers = filteredCustomers.slice(indexOfFirstCustomer, indexOfLastCustomer);

  const paginate = (pageNumber) => setCurrentPage(pageNumber);

  const renderModalContent = () => {
    if (modalMode === 'view') {
      return (
        <div className="grid grid-cols-2 gap-6">
          <div>
            <h4 className="text-lg font-semibold mb-4">Personal Information</h4>
            <div className="space-y-3">
              <p><span className="font-medium">Name:</span> {currentCustomer.firstName} {currentCustomer.lastName}</p>
              <p><span className="font-medium">Email:</span> {currentCustomer.email}</p>
              <p><span className="font-medium">Phone:</span> {currentCustomer.phone}</p>
              <p><span className="font-medium">Category:</span> {currentCustomer.category}</p>
            </div>
          </div>
          <div>
            <h4 className="text-lg font-semibold mb-4">Address Information</h4>
            <div className="space-y-3">
              <p><span className="font-medium">Street:</span> {currentCustomer.street}</p>
              <p><span className="font-medium">City:</span> {currentCustomer.city}</p>
              <p><span className="font-medium">Country:</span> {currentCustomer.country}</p>
              <p><span className="font-medium">Zip Code:</span> {currentCustomer.zipcode}</p>
            </div>
          </div>
          <div className="col-span-2">
            <h4 className="text-lg font-semibold mb-4">Identification</h4>
            <div className="space-y-3">
              <p><span className="font-medium">ID Type:</span> {currentCustomer.idType}</p>
              <p><span className="font-medium">ID State/Country:</span> {currentCustomer.idStateCountry}</p>
              <p><span className="font-medium">ID Number:</span> {currentCustomer.idNumber}</p>
            </div>
          </div>
        </div>
      );
    } else {
      return (
        <form onSubmit={handleSubmit} className="grid grid-cols-2 gap-6">
          <div>
            <h4 className="text-lg font-semibold mb-4">Personal Information</h4>
            <div className="space-y-3">
              <div>
                <label className="block text-sm font-medium text-gray-700 dark:text-gray-300">First Name</label>
                <input
                  type="text" name="firstName"
                  value={currentCustomer.firstName} onChange={handleInputChange}
                  className="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-300 focus:ring focus:ring-indigo-200 focus:ring-opacity-50"
                  required
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700 dark:text-gray-300">Last Name</label>
                <input
                  type="text" name="lastName"
                  value={currentCustomer.lastName} onChange={handleInputChange}
                  className="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-300 focus:ring focus:ring-indigo-200 focus:ring-opacity-50"
                  required
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700 dark:text-gray-300">Email</label>
                <input
                  type="email" name="email"
                  value={currentCustomer.email} onChange={handleInputChange}
                  className="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-300 focus:ring focus:ring-indigo-200 focus:ring-opacity-50"
                  required
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700 dark:text-gray-300">Phone</label>
                <input
                  type="tel" name="phone"
                  value={currentCustomer.phone} onChange={handleInputChange}
                  className="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-300 focus:ring focus:ring-indigo-200 focus:ring-opacity-50"
                  required
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700 dark:text-gray-300">Category</label>
                <select
                  name="category"
                  value={currentCustomer.category}
                  onChange={handleInputChange}
                  className="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-300 focus:ring focus:ring-indigo-200 focus:ring-opacity-50"
                  required
                >
                  <option value="Individual">Individual</option>
                  <option value="Business">Business</option>
                </select>
              </div>
            </div>
          </div>
          <div>
            <h4 className="text-lg font-semibold mb-4">Address Information</h4>
            <div className="space-y-3">
              <div>
                <label className="block text-sm font-medium text-gray-700 dark:text-gray-300">Street</label>
                <input
                  type="text" name="street"
                  value={currentCustomer.street} onChange={handleInputChange}
                  className="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-300 focus:ring focus:ring-indigo-200 focus:ring-opacity-50"
                  required
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700 dark:text-gray-300">City</label>
                <input
                  type="text" name="city"
                  value={currentCustomer.city} onChange={handleInputChange}
                  className="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-300 focus:ring focus:ring-indigo-200 focus:ring-opacity-50"
                  required
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700 dark:text-gray-300">Country</label>
                <input
                  type="text" name="country"
                  value={currentCustomer.country} onChange={handleInputChange}
                  className="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-300 focus:ring focus:ring-indigo-200 focus:ring-opacity-50"
                  required
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700 dark:text-gray-300">Zip Code</label>
                <input
                  type="text" name="zipcode"
                  value={currentCustomer.zipcode} onChange={handleInputChange}
                  className="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-300 focus:ring focus:ring-indigo-200 focus:ring-opacity-50"
                  required
                />
              </div>
            </div>
          </div>
          <div className="col-span-2">
            <h4 className="text-lg font-semibold mb-4">Identification</h4>
            <div className="space-y-3">
              <div>
                <label className="block text-sm font-medium text-gray-700 dark:text-gray-300">ID Type</label>
                <input
                  type="text" name="idType"
                  value={currentCustomer.idType} onChange={handleInputChange}
                  className="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-300 focus:ring focus:ring-indigo-200 focus:ring-opacity-50"
                  required
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700 dark:text-gray-300">ID State/Country</label>
                <input
                  type="text" name="idStateCountry"
                  value={currentCustomer.idStateCountry} onChange={handleInputChange}
                  className="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-300 focus:ring focus:ring-indigo-200 focus:ring-opacity-50"
                  required
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700 dark:text-gray-300">ID Number</label>
                <input
                  type="text" name="idNumber"
                  value={currentCustomer.idNumber} onChange={handleInputChange}
                  className="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-300 focus:ring focus:ring-indigo-200 focus:ring-opacity-50"
                  required
                />
              </div>
            </div>
          </div>
        </form>
      );
    }
  };

  return (
    <div className="container mx-auto px-4 py-8">
      <h1 className="text-3xl font-bold mb-6 text-gray-800 dark:text-white">Customer Management</h1>
      
      <div className="mb-4 flex justify-between items-center">
        <div className="relative">
          <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400" />
          <input
            type="text"
            placeholder="Search customers"
            className="pl-10 pr-4 py-2 border rounded-md focus:outline-none focus:ring-1 focus:ring-blue-600 dark:bg-gray-700 dark:border-gray-600 dark:text-white"
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
          />
        </div>
        <button
          onClick={() => {
            resetCurrentCustomer();
            setModalMode('add');
            setIsModalOpen(true);
          }}
          className="px-4 py-2 bg-blue-600 text-white rounded-md hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-blue-600 focus:ring-opacity-50"
        >
          <Plus size={18} className="inline mr-2" />
          Add Customer
        </button>
      </div>

      <div className="bg-white dark:bg-gray-800 shadow-md rounded-lg overflow-hidden">
        <table className="min-w-full divide-y divide-gray-200 dark:divide-gray-700">
          <thead className="bg-gray-50 dark:bg-gray-700">
            <tr>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 dark:text-gray-300 uppercase tracking-wider">Name</th>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 dark:text-gray-300 uppercase tracking-wider">Email</th>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 dark:text-gray-300 uppercase tracking-wider">Phone</th>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 dark:text-gray-300 uppercase tracking-wider">Category</th>
            </tr>
          </thead>
          <tbody className="bg-white divide-y divide-gray-200 dark:bg-gray-800 dark:divide-gray-700">
            {currentCustomers.map((customer) => (
              <tr key={customer._id} onClick={() => handleRowClick(customer)} className="cursor-pointer hover:bg-gray-50 dark:hover:bg-gray-700">
                <td className="px-6 py-4 whitespace-nowrap">
                  <div className="text-sm font-medium text-gray-900 dark:text-white">{customer.firstName} {customer.lastName}</div>
                </td>
                <td className="px-6 py-4 whitespace-nowrap">
                  <div className="text-sm text-gray-500 dark:text-gray-300">{customer.email}</div>
                </td>
                <td className="px-6 py-4 whitespace-nowrap">
                  <div className="text-sm text-gray-500 dark:text-gray-300">{customer.phone}</div>
                </td>
                <td className="px-6 py-4 whitespace-nowrap">
                  <span className={`px-2 inline-flex text-xs leading-5 font-semibold rounded-full ${
                    customer.category === 'Individual' ? 'bg-green-100 text-green-800' : 'bg-blue-100 text-blue-800'
                  }`}>
                    {customer.category}
                  </span>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>

      <div className="mt-4 flex justify-center">
        <nav className="relative z-0 inline-flex rounded-md shadow-sm -space-x-px" aria-label="Pagination">
          {Array.from({ length: Math.ceil(filteredCustomers.length / customersPerPage) }).map((_, index) => (
            <button
              key={index}
              onClick={() => paginate(index + 1)}
              className={`relative inline-flex items-center px-4 py-2 border text-sm font-medium ${
                currentPage === index + 1
                  ? 'z-10 bg-blue-50 border-blue-500 text-blue-600'
                  : 'bg-white border-gray-300 text-gray-500 hover:bg-gray-50'
              }`}
            >
              {index + 1}
            </button>
          ))}
        </nav>
      </div>

      {isModalOpen && (
        <div className="fixed inset-0 bg-gray-600 bg-opacity-50 overflow-y-auto h-full w-full" id="my-modal">
          <div className="relative top-20 mx-auto p-5 border w-11/12 max-w-4xl shadow-lg rounded-md bg-white dark:bg-gray-800">
            <div className="mt-3">
              <div className="flex justify-between items-center mb-4">
                <h3 className="text-lg font-medium text-gray-900 dark:text-white">
                  {modalMode === 'view' ? 'Customer Details' : modalMode === 'edit' ? 'Edit Customer' : 'Add New Customer'}
                </h3>
                <button onClick={() => setIsModalOpen(false)} className="text-gray-400 hover:text-gray-500">
                  <X size={24} />
                </button>
              </div>
              {renderModalContent()}
              <div className="flex justify-end mt-4 space-x-2">
                {modalMode === 'view' && (
                  <>
                    <button
                      onClick={handleEdit}
                      className="px-4 py-2 bg-blue-600 text-white rounded-md hover:bg-blue-700"
                    >
                      <Edit size={16} className="inline mr-2" />
                      Edit
                    </button>
                    <button
                      onClick={handleDelete}
                      className="px-4 py-2 bg-red-600 text-white rounded-md hover:bg-red-700"
                    >
                      <Trash2 size={16} className="inline mr-2" />
                      Delete
                    </button>
                  </>
                )}
                {(modalMode === 'edit' || modalMode === 'add') && (
                  <>
                    <button
                      onClick={() => modalMode === 'edit' ? setModalMode('view') : setIsModalOpen(false)}
                      className="px-4 py-2 bg-gray-300 text-gray-800 rounded-md hover:bg-gray-400"
                    >
                      Cancel
                    </button>
                    <button
                      onClick={handleSubmit}
                      className="px-4 py-2 bg-blue-600 text-white rounded-md hover:bg-blue-700"
                    >
                      {modalMode === 'edit' ? 'Update' : 'Add'} Customer
                    </button>
                  </>
                )}
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}

export default CustomerManagement;
