// src/components/Navigation.js
import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import { 
  Home, 
  Users, 
  Package, 
  UserCheck, 
  User,
  Sun,
  Moon,
  LogOut
} from 'lucide-react';

function Navigation({ onLogout, toggleDarkMode, isDarkMode }) {
  const [isProfileMenuOpen, setIsProfileMenuOpen] = useState(false);

  return (
    <nav className="bg-gray-800 text-white p-4">
      <div className="container mx-auto flex justify-between items-center">
        <ul className="flex space-x-6">
          <li>
            <Link to="/dashboard" className="flex items-center hover:text-gray-300">
              <Home size={18} className="mr-2" />
              <span>Dashboard</span>
            </Link>
          </li>
          <li>
            <Link to="/shipments" className="flex items-center hover:text-gray-300">
              <Package size={18} className="mr-2" />
              <span>Shipments</span>
            </Link>
          </li>
          <li>
            <Link to="/customers" className="flex items-center hover:text-gray-300">
              <Users size={18} className="mr-2" />
              <span>Customers</span>
            </Link>
          </li>
          <li>
            <Link to="/users" className="flex items-center hover:text-gray-300">
              <UserCheck size={18} className="mr-2" />
              <span>Users</span>
            </Link>
          </li>
        </ul>
        <div className="relative">
          <button
            onClick={() => setIsProfileMenuOpen(!isProfileMenuOpen)}
            className="flex items-center space-x-2 focus:outline-none"
          >
            <User size={18} />
            <span>Profile</span>
          </button>
          {isProfileMenuOpen && (
            <div className="absolute right-0 mt-2 w-48 bg-white dark:bg-gray-700 rounded-md shadow-lg py-1 z-10">
              <Link to="/profile" className="flex items-center px-4 py-2 text-sm text-gray-700 dark:text-gray-200 hover:bg-gray-100 dark:hover:bg-gray-600">
                <User size={16} className="mr-2" />
                <span>Your Profile</span>
              </Link>
              <button
                onClick={toggleDarkMode}
                className="flex items-center w-full text-left px-4 py-2 text-sm text-gray-700 dark:text-gray-200 hover:bg-gray-100 dark:hover:bg-gray-600"
              >
                {isDarkMode ? <Sun size={16} className="mr-2" /> : <Moon size={16} className="mr-2" />}
                <span>{isDarkMode ? 'Light Mode' : 'Dark Mode'}</span>
              </button>
              <button
                onClick={onLogout}
                className="flex items-center w-full text-left px-4 py-2 text-sm text-gray-700 dark:text-gray-200 hover:bg-gray-100 dark:hover:bg-gray-600"
              >
                <LogOut size={16} className="mr-2" />
                <span>Logout</span>
              </button>
            </div>
          )}
        </div>
      </div>
    </nav>
  );
}

export default Navigation;