// src/components/ShipmentManagement/ShipmentModal.js
import React, { useState, useRef } from 'react';
import { X, Edit, Trash2, PlusCircle, MinusCircle, Printer } from 'lucide-react';
import Select from 'react-select';
import { useReactToPrint } from 'react-to-print';
import PrintReceipt from './PrintReceipt';
import PrintLabel from './PrintLabel';

function ShipmentModal({ shipment, customers, mode, onClose, onSave, onDelete }) {
  const [currentShipment, setCurrentShipment] = useState(shipment);
  const receiptRef = useRef();
  const labelRef = useRef();

  const handlePrintReceipt = useReactToPrint({    pageStyle: `@media print {
    @page {
      size: Letter potrait;
      margin: 0;
    }
  }`,
    content: () => receiptRef.current,
  });

  const handlePrintLabel = useReactToPrint({    
    pageStyle: `@media print {
    @page {
      size: A5 potrait;
      margin: 0;
    }
  }`,
    content: () => labelRef.current,
  });

  const handleInputChange = (e) => {
    const { name, value, type, checked } = e.target;
    setCurrentShipment(prev => ({
      ...prev,
      [name]: type === 'checkbox' ? checked : value
    }));
  };

  const handleItemChange = (index, field, value) => {
    const updatedItems = currentShipment.items.map((item, i) => {
      if (i === index) {
        const updatedItem = { ...item, [field]: value };
        updatedItem.total = updatedItem.quantity * updatedItem.price;
        return updatedItem;
      }
      return item;
    });
    setCurrentShipment(prev => ({ ...prev, items: updatedItems }));
  };

  const addItem = () => {
    setCurrentShipment(prev => ({
      ...prev,
      items: [...prev.items, { name: '', quantity: 0, type: 'Piece', price: 0, total: 0 }]
    }));
  };

  const removeItem = (index) => {
    setCurrentShipment(prev => ({
      ...prev,
      items: prev.items.filter((_, i) => i !== index)
    }));
  };

  const calculateTotal = () => {
    const itemsTotal = currentShipment.items.reduce((sum, item) => sum + item.total, 0);
    const subtotal = itemsTotal + currentShipment.serviceFee;
    const discountAmount = (subtotal * currentShipment.discountPercentage) / 100;
    return subtotal - discountAmount;
  };

  const renderContent = () => {
    if (mode === 'view') {
      return (
        <>
          <div className="grid grid-cols-2 gap-6">
            <div>
              <h4 className="text-lg font-semibold mb-4">Shipment Details</h4>
              <p><strong>Tracking Number:</strong> {currentShipment.trackingNumber}</p>
              <p><strong>Sender:</strong> {currentShipment.sender}</p>
              <p><strong>Receiver:</strong> {currentShipment.receiver}</p>
              <p><strong>Status:</strong> {currentShipment.status}</p>
              <p><strong>Shipment Destination:</strong> {currentShipment.shipmentDestination}</p>
              <p><strong>Receive Package Date:</strong> {new Date(currentShipment.receiveDate).toLocaleDateString()}</p>
            </div>
            <div>
              <h4 className="text-lg font-semibold mb-4">Items</h4>
              <table className="w-full">
                <thead>
                  <tr>
                    <th className="text-left">Item</th>
                    <th className="text-right">Quantity</th>
                    <th className="text-right">Type</th>
                    <th className="text-right">Price</th>
                    <th className="text-right">Total</th>
                  </tr>
                </thead>
                <tbody>
                  {currentShipment.items.map((item, index) => (
                    <tr key={index}>
                      <td>{item.name}</td>
                      <td className="text-right">{item.quantity}</td>
                      <td className="text-right">{item.type}</td>
                      <td className="text-right">${item.price.toFixed(2)}</td>
                      <td className="text-right">${item.total.toFixed(2)}</td>
                    </tr>
                  ))}
                </tbody>
                <tfoot>
                  <tr>
                    <td colSpan="4" className="text-right font-medium">Service Fee:</td>
                    <td className="text-right">${currentShipment.serviceFee.toFixed(2)}</td>
                  </tr>
                  <tr>
                    <td colSpan="4" className="text-right font-medium">Discount:</td>
                    <td className="text-right">{currentShipment.discountPercentage}%</td>
                  </tr>
                  <tr>
                    <td colSpan="4" className="text-right font-medium">Total:</td>
                    <td className="text-right">${calculateTotal().toFixed(2)}</td>
                  </tr>
                </tfoot>
              </table>
            </div>
          </div>
          <div className="mt-4 flex justify-end space-x-2">
            <button
              onClick={handlePrintReceipt}
              className="px-4 py-2 bg-green-600 text-white rounded-md hover:bg-green-700 flex items-center"
            >
              <Printer size={18} className="mr-2" />
              Print Receipt
            </button>
            <button
              onClick={handlePrintLabel}
              className="px-4 py-2 bg-blue-600 text-white rounded-md hover:bg-blue-700 flex items-center"
            >
              <Printer size={18} className="mr-2" />
              Print Label
            </button>
          </div>
          <div style={{ display: 'none' }}>
            <PrintReceipt ref={receiptRef} shipment={currentShipment} />
            <PrintLabel ref={labelRef} shipment={currentShipment} />
          </div>
        </>
      );
    } else {
      return (
        <form onSubmit={(e) => { e.preventDefault(); onSave(currentShipment); }} className="space-y-6">
          <div className="grid grid-cols-2 gap-6">
            <div>
              <label className="block text-sm font-medium text-gray-700 dark:text-gray-300">Sender</label>
              <Select
                options={customers.map(c => ({ value: c._id, label: c.fullName }))}
                value={{ value: currentShipment.sender, label: currentShipment.sender }}
                onChange={(selected) => handleInputChange({ target: { name: 'sender', value: selected.label } })}
                className="mt-1"
              />
            </div>
            <div>
              <label className="block text-sm font-medium text-gray-700 dark:text-gray-300">Receiver</label>
              <Select
                options={customers.map(c => ({ value: c._id, label: c.fullName }))}
                value={{ value: currentShipment.receiver, label: currentShipment.receiver }}
                onChange={(selected) => handleInputChange({ target: { name: 'receiver', value: selected.label } })}
                className="mt-1"
              />
            </div>
            <div>
              <label className="block text-sm font-medium text-gray-700 dark:text-gray-300">Shipment Destination</label>
              <select
                name="shipmentDestination"
                value={currentShipment.shipmentDestination}
                onChange={handleInputChange}
                className="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-300 focus:ring focus:ring-indigo-200 focus:ring-opacity-50"
              >
                <option value="Delmas">Delmas</option>
                <option value="Cap-Haitian">Cap-Haitian</option>
              </select>
            </div>
            <div>
              <label className="block text-sm font-medium text-gray-700 dark:text-gray-300">Receive Package Date</label>
              <input
                type="date"
                name="receiveDate"
                value={currentShipment.receiveDate}
                onChange={handleInputChange}
                className="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-300 focus:ring focus:ring-indigo-200 focus:ring-opacity-50"
              />
            </div>
          </div>
          
          <div className="bg-gray-50 p-6 rounded-lg shadow">
            <h4 className="text-lg font-semibold mb-4">Items</h4>
            <table className="w-full">
              <thead>
                <tr className="border-b">
                  <th className="text-left pb-2">Item</th>
                  <th className="text-right pb-2">Quantity</th>
                  <th className="text-right pb-2">Qty Type</th>
                  <th className="text-right pb-2">Price</th>
                  <th className="text-right pb-2">Total</th>
                  <th className="text-right pb-2">Action</th>
                </tr>
              </thead>
              <tbody>
                {currentShipment.items.map((item, index) => (
                  <tr key={index} className="border-b">
                    <td className="py-2">
                      <input
                        type="text"
                        value={item.name}
                        onChange={(e) => handleItemChange(index, 'name', e.target.value)}
                        className="w-full bg-transparent border-none focus:ring-0"
                        required
                      />
                    </td>
                    <td className="py-2">
                      <input
                        type="number"
                        value={item.quantity}
                        onChange={(e) => handleItemChange(index, 'quantity', Number(e.target.value))}
                        className="w-full bg-transparent border-none focus:ring-0 text-right"
                        min="0"
                        required
                      />
                    </td>
                    <td className="py-2">
                      <select
                        value={item.type}
                        onChange={(e) => handleItemChange(index, 'type', e.target.value)}
                        className="w-full bg-transparent border-none focus:ring-0 text-right"
                        required
                      >
                        <option value="Piece">Piece</option>
                        <option value="Lb">Lb</option>
                      </select>
                    </td>
                    <td className="py-2">
                      <input
                        type="number"
                        value={item.price}
                        onChange={(e) => handleItemChange(index, 'price', Number(e.target.value))}
                        className="w-full bg-transparent border-none focus:ring-0 text-right"
                        min="0"
                        step="0.01"
                        required
                      />
                    </td>
                    <td className="py-2 text-right">${item.total.toFixed(2)}</td>
                    <td className="py-2 text-right">
                      <button
                        type="button"
                        onClick={() => removeItem(index)}
                        className="text-red-600 hover:text-red-900"
                      >
                        <MinusCircle size={18} />
                      </button>
                    </td>
                  </tr>
                ))}
              </tbody>
              <tfoot>
                <tr>
                  <td colSpan="6" className="pt-2">
                    <button
                      type="button"
                      onClick={addItem}
                      className="text-green-600 hover:text-green-900"
                    >
                      <PlusCircle size={18} />
                    </button>
                  </td>
                </tr>
              </tfoot>
            </table>

            <div className="mt-4 space-y-2">
              <div className="flex justify-between items-center">
                <span>Service Fee:</span>
                <input
                  type="number"
                  name="serviceFee"
                  value={currentShipment.serviceFee}
                  onChange={handleInputChange}
                  className="w-24 bg-transparent border-none focus:ring-0 text-right"
                  min="0"
                  step="0.01"
                />
              </div>
              <div className="flex justify-between items-center">
                <span>Discount Percentage:</span>
                <input
                  type="number"
                  name="discountPercentage"
                  value={currentShipment.discountPercentage}
                  onChange={handleInputChange}
                  className="w-24 bg-transparent border-none focus:ring-0 text-right"
                  min="0"
                  max="100"
                  step="0.01"
                />
              </div>
              <div className="flex justify-between items-center text-lg font-bold">
                <span>Total:</span>
                <span>${calculateTotal().toFixed(2)}</span>
              </div>
            </div>
          </div>

          <div className="flex justify-end space-x-2">
            <button
              type="button"
              onClick={onClose}
              className="px-4 py-2 bg-gray-300 text-gray-800 rounded-md hover:bg-gray-400"
            >
              Cancel
            </button>
            <button
              type="submit"
              className="px-4 py-2 bg-blue-600 text-white rounded-md hover:bg-blue-700"
            >
              {mode === 'edit' ? 'Update' : 'Add'} Shipment
            </button>
          </div>
        </form>
      );
    }
  };

  return (
    <div className="fixed inset-0 bg-gray-600 bg-opacity-50 overflow-y-auto h-full w-full" id="my-modal">
      <div className="relative top-20 mx-auto p-5 border w-11/12 max-w-4xl shadow-lg rounded-md bg-white">
        <div className="mt-3">
          <div className="flex justify-between items-center mb-4">
            <h3 className="text-xl font-semibold text-gray-900">
              {mode === 'view' ? 'Shipment Details' : mode === 'edit' ? 'Edit Shipment' : 'Add New Shipment'}
            </h3>
            <button onClick={onClose} className="text-gray-400 hover:text-gray-500">
              <X size={24} />
            </button>
          </div>
          {renderContent()}
        </div>
      </div>
    </div>
  );
}

export default ShipmentModal;