import React, { useState, useEffect } from 'react';
import { BrowserRouter as Router, Route, Routes, Navigate } from 'react-router-dom';
import Login from './components/Login';
import ForgotPassword from './components/ForgotPassword';
import Dashboard from './components/Dashboard';
import CustomerManagement from './components/CustomerManagement';
import ShipmentManagement from './components/ShipmentManagement/ShipmentManagement';
import UserManagement from './components/UserManagement';
import Navigation from './components/Navigation';

function App() {
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [isDarkMode, setIsDarkMode] = useState(false);

  useEffect(() => {
    // Check if dark mode preference is stored in localStorage
    const storedDarkMode = localStorage.getItem('darkMode');
    if (storedDarkMode !== null) {
      setIsDarkMode(JSON.parse(storedDarkMode));
    }

    // Check if the user is authenticated by looking for a token
    const token = localStorage.getItem('token');
    if (token) {
      setIsAuthenticated(true);
    }
  }, []);

  useEffect(() => {
    // Apply dark mode class to body
    if (isDarkMode) {
      document.body.classList.add('dark');
    } else {
      document.body.classList.remove('dark');
    }
    // Store dark mode preference in localStorage
    localStorage.setItem('darkMode', JSON.stringify(isDarkMode));
  }, [isDarkMode]);

  const handleLogin = () => {
    // Set the isAuthenticated state to true when the user logs in
    setIsAuthenticated(true);
  };

  const handleLogout = () => {
    // Clear the token from localStorage and update state
    localStorage.removeItem('token');
    setIsAuthenticated(false);
  };

  const toggleDarkMode = () => {
    setIsDarkMode(!isDarkMode);
  };

  return (
    <Router>
      <div className={`min-h-screen ${isDarkMode ? 'dark' : ''}`}>
        <div className="bg-gray-100 dark:bg-gray-900 min-h-screen">
          {isAuthenticated && (
            <Navigation
              onLogout={handleLogout}
              toggleDarkMode={toggleDarkMode}
              isDarkMode={isDarkMode}
            />
          )}
          <Routes>
            <Route path="/" element={
              isAuthenticated ? <Navigate to="/dashboard" /> : <Login onLogin={handleLogin} />
            } />
            <Route path="/forgot-password" element={<ForgotPassword />} />
            <Route path="/dashboard" element={
              isAuthenticated ? <Dashboard /> : <Navigate to="/" />
            } />
            <Route path="/shipments" element={
              isAuthenticated ? <ShipmentManagement /> : <Navigate to="/" />
            } />
            <Route path="/customers" element={
              isAuthenticated ? <CustomerManagement /> : <Navigate to="/" />
            } />
            <Route path="/users" element={
              isAuthenticated ? <UserManagement /> : <Navigate to="/" />
            } />
            <Route path="*" element={
              isAuthenticated ? <Navigate to="/dashboard" /> : <Navigate to="/" />
            } />
          </Routes>
        </div>
      </div>
    </Router>
  );
}

export default App;
